.nav__list {
  @include list-style-reset;
  text-align: left;

  @media (max-width: $tablet) {
    display: block;
  }

  &--catalog,
  &--about {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &--catalog {
    @media (max-width: $tablet) {
      display: block;
      font-size: 24px;
      line-height: 37px;
    }
  }

  &--about {
    display: none;
    margin-bottom: 74px;
    font-size: 18px;
    line-height: 24px;
    color: rgba($white, 0.75);
  }
}

.nav__list--catalog .nav__item:not(:last-child) {
  margin-right: 35px;
}

.nav__link {
  display: block;
  line-height: 40px;
  text-decoration: none;
  color: inherit;
  transition: color $easy;

  &:hover,
  &:active {
    color: $gold;
  }
}

.nav__button {
  display: none;

  &--show {
    @media (max-width: $tablet) {
      padding: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: absolute;
      top: 18px;
      left: -10px;
      width: 46px;
      height: 38px;
    }
  }
}

.nav__button-lines {
  display: block;
  width: 100%;
  height: 2px;
  background: $basic-black;
}

@media (max-width: $tablet) {
  .nav--opened .nav__button {
    height: 40px;
  }

  .nav--opened .nav__list--about {
    display: block;
  }

  .nav--opened .nav__button-lines {
    background: $white;
  }

  .nav--opened .nav__button-lines--top {
    transform-origin: 0% 0%;
    transform: rotate(45deg);
    transition: transform 0.4s ease-in-out;
  }

  .nav--opened .nav__button-lines--middle {
    transform-origin: 0% 0%;
    transform: scaleY(0);
    transition: transform 0.2s ease-in-out;
  }

  .nav--opened .nav__button-lines--bottom {
    transform-origin: 0% 100%;
    transform: rotate(-45deg);
    transition: transform 0.4s ease-in-out;
  }

  .nav--closed .nav__list {
    display: none;
  }

  .nav--closed ~ form {
    display: none;
  }

  .nav--nojs .nav__button {
    display: none;
  }

  .nav--nojs .nav__list {
    display: block;
  }
}
